<template>
  <div ref="cloud" class="container"></div>
</template>

<script>
import 'echarts-wordcloud'
export default {
    name:'WordCloud',
    data(){
        return {
            myChart:'',
        }
    },
    props:['title','chartData','isTop'],
    watch:{
        chartData(){
            this.setChart()
        }
    },
    computed:{
        formatData(){
            const result = []
            for(let key in this.chartData){
                result.push({
                    name:key,
                    value:this.chartData[key]
                })
            }
            return result
        },
    },
    methods:{
        setChart(){
            this.myChart = this.$echarts.init(this.$refs.cloud)
            // this.myChart = this.$echarts.init(this.$refs.cloud, 'dark');
            if(!this.title){
                this.myChart.showLoading();
            }
            else{
                this.myChart.hideLoading();
                let option = {
                    title:{
                        text:this.title,
                        subtext:`223%`,
                        // textStyle:this.isTop?{}:{color:'white'},
                        subtextStyle:{color:"#faf"}
                    },
                    tooltip:{
                        trigger:'item',
                        formatter:(params) =>{
                            return ''+params.name + ' : '+params.value+'';
                        }
                    },
                    series: [{
                        type: 'wordCloud',
                        // 
                        gridSize: 20,
                        rotationRange: [0, 0],
                        sizeRange: [12, 36],
                        // 
                        shape: 'circle',
                        // 
                        textStyle: {
                            normal: {
                                fontFamily: 'sans-serif',
                                fontWeight: 'bold',
                                // 
                                color: function () {
                                    return 'rgb(' + [
                                        Math.round(Math.random() * 255),
                                        Math.round(Math.random() * 255),
                                        Math.round(Math.random() * 255)
                                    ].join(',') + ')';
                                }
                            }
                        },
                        // 
                        data: this.formatData
                    }]
                }
                option && this.myChart.setOption(option);
                this.myChart.on('click',(e)=>{
                    e.event.cancelBubble = true
                    this.$bus.$emit('getChart',{name:this.$options.name,title:this.title,chartData:this.chartData,other:this.other || ''})
                })
            }
        },
    },
    mounted(){
        this.setChart()
    }
}
</script>

