<template>
  <div class="cmapBox">
    <div ref="map" class="chinaMap"></div>
    <div class="mapNO">GS(2016)2923</div>
  </div>
  
</template>

<script>
import axios from "axios";

export default {
  name: "ChinaMap",
  data() {
    return {
      myChart: "",
      nameMap:{
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
              : "",
      },
      oData:{
        "": 1471,
        "": 681,
        "": 355,
        "": 241,
        "": 212,
        "": 199,
        "": 155,
        "": 121,
        "": 117,
        "": 93,
        "": 63,
        "": 63,
        "": 50,
        "": 48,
        "": 47,
        "": 44,
        "": 36,
        "": 36,
        "": 33,
        "": 33,
        "": 32,
        "": 31,
        "": 28,
        "": 24,
        "": 21,
        "": 21,
        "": 19,
        "": 18,
        "": 16,
        "": 14,
        "": 5,
        "": 5
      },
      isTimer:false,
      timer:''
    };
  },
  props:['title','chartData','isTop'],
      watch:{
        chartData(){
          this.setChart().then((value) => {
          var option;
          this.myChart.hideLoading();
          this.$echarts.registerMap("China", value);
          this.myChart.setOption(
            (option = {
              title: {
                text: this.title,
                left: "center",
              },
              tooltip: {
                trigger: "item",
                formatter: "{b}<br/>{c}",
              },
              toolbox: {
                show: false,
                orient: "vertical",
                left: "right",
                top: "center",
                feature: {
                  dataView: { readOnly: false },
                  restore: {},
                  savaAsImage: {},
                },
              },
              visualMap: {
                // type: "piecewise",
                // min: 0,
                // max: 4000,
                // splitNumber: 8,
                // pieces: [
                //   { min: 0, max: 0, color: "white" }, 
                //   { min: 1, max: 10, color: "skyblue" }, 
                //   { min: 10, max: 30, color: "#ffa500" },
                //   { min: 30, max: 50, color: "#80f460" },
                //   { min: 50, max: 100, color: "#c3d461" }, 
                //   { min: 100, max: 150, color: "#00FF00" },
                //   { min: 150, max: 200, color: "#0000FF" }, 
                //   { min: 1000, max: 4000, color: "red" },
                // ],
                left:'30',
                top:'0'

              },
              series: [
                {
                  name: "china",
                  type: "map",
                  map: "China",
                  center:[91.195397, 35.86166],
                  zoom:1.7,
                  // roam: true,
                  label: {
                    show: true,
                    fontSize:this.isTop?18:6,
                    color:'#666'
                  },
                  data: this.formatData,
                  nameMap: this.nameMap,
                },
              ],
            }),
            
          );
          option && this.myChart.setOption(option);
          if(!this.isTop && !this.timer) this.addTimer()
      });
        }
    },
  computed:{
    formatData(){
      const result = []
      if(this.chartData){
        for(let key in this.nameMap){
          const obj = {name:'',value:0}
          obj.name = key
          if(Object.prototype.hasOwnProperty.call(this.chartData, key)){
            obj.value = this.chartData[key]
          }
          result.push(obj)
        }
      }
      
      return result
    },
    formatOData(){
      const result = []
      if(this.oData){
        for(let key in this.nameMap){
          const obj = {name:'',value:0}
          obj.name = key
          if(Object.prototype.hasOwnProperty.call(this.oData, key)){
            obj.value = this.oData[key]
          }
          result.push(obj)
        }
      }
      
      return result
    }
  },
  methods: {
    setChart() {
      return new Promise((resolve, reject) => {
        axios
          .get("https://xump.cn/datavApi/datav/china")
          .then(
            (res) => {
              resolve(res.data);
            },
            (err) => {
              reject(err);
            }
          );
      });
    },
    addTimer(){
      this.timer = setInterval(()=>{
        this.myChart.setOption({
          title:{
            text:this.isTimer?'':this.title
          },
          series: [{
            data: this.isTimer?this.formatOData:this.formatData // 
          }]
        });
        this.isTimer = !this.isTimer
      },3000)
    }
  },
  created() {
    // this.isTimer = this.isOther?true:false
  },
  mounted() {
    this.myChart = this.$echarts.init(this.$refs.map);
    if(!this.title){
      this.myChart.showLoading();
    }
    else{
      this.setChart().then((value) => {
        var option;
        const vmap = {
          // type: "piecewise",
          // min: 0,
          // max: 4000,
          // splitNumber: 8,
          // pieces: [
          //   { min: 0, max: 0, color: "white" }, 
          //   { min: 1, max: 10, color: "skyblue" }, 
          //   { min: 10, max: 30, color: "#ffa500" },
          //   { min: 30, max: 50, color: "#80f460" },
          //   { min: 50, max: 100, color: "#c3d461" }, 
          //   { min: 100, max: 150, color: "#00FF00" },
          //   { min: 150, max: 200, color: "#0000FF" }, 
          //   { min: 1000, max: 4000, color: "red" },
          // ],
          left:'30',
          top:'20',
          // textStyle:this.isTop?{}:{color:'white'}
        }
        this.myChart.hideLoading();
        this.$echarts.registerMap("China", value);
        this.myChart.setOption(
          (option = {
            title: {
              text: this.title,
              left: "center",
              top:this.isTop?20:0,
              // textStyle:this.isTop?{fontSize:30,color:'#000'}:{color:'#fff'},
            },
            tooltip: {
              trigger: "item",
              formatter: "{b}<br/>{c}",
            },
            toolbox: {
              show: false,
              orient: "vertical",
              left: "right",
              top: "center",
              feature: {
                dataView: { readOnly: false },
                restore: {},
                savaAsImage: {},
              },
            },
            visualMap: this.isTop?{left:'30',top:'20',text:['','']}:vmap,
            series: [
              {
                name: "china",
                type: "map",
                map: "China",
                center:this.isTop?[102.195397, 35.86166]:[91.195397, 35.86166],
                zoom:1.7,
                roam: this.isTop,
                label: {
                  show: true,
                  fontSize:this.isTop?12:6,
                  color:'#666'
                },
                data: this.formatData,
                nameMap: this.nameMap,
              },
            ],
          }),
          
        );
        option && this.myChart.setOption(option);
        if(!this.isTop) this.addTimer()
      });
    }
    if(this.isTop){
        this.myChart.on('click',(e)=>{
          e.event.cancelBubble = true
          console.log('')
        })
    }
    else{
      this.myChart.on('click',(e)=>{
        e.event.cancelBubble = true
        this.$bus.$emit('getChart',{name:this.$options.name,title:this.title,chartData:this.chartData,isOther:this.isTimer})
      })
    }
  },
};
</script>

<style>
.cmapBox{
  position: relative;
}
.chinaMap{
  width:100%;
  height:100%
}
.mapNO{
  position: absolute;
  right:0;
  bottom: 0;
  font-size: 12px;
}
